import React, { ChangeEvent, useContext, useEffect, useState } from "react";
import {
	Box,
	Text,
	FormControl,
	FormLabel,
	Input,
	Flex,
	Button,
	FormErrorMessage,
	RadioGroup,
	Radio
} from "@chakra-ui/react";
import DefaultAPI from "../../api/DefaultAPI";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { UserContext } from "../../context/UserContext";
import openToast from "../../utils/OpenToast";

interface signUpRequest {
	userId: string;
	password: string;
	passwordCheck: string;
	level: number;
}

const errorCodeMapping: Record<number, string> = {
	4010: "    .",
	4000: "  ."
};

const handleErrorMessage = (statusCode: number): string => {
	return errorCodeMapping[statusCode] || " .";
};

const passwordRegex = /^(?=.*[a-zA-Z0-9])(?=.*[^a-zA-Z0-9]).{8,}$/;

const apiSignUp = async (data: signUpRequest): Promise<void> => {
	try {
		await DefaultAPI.post("/auth/signup", data, {
			withCredentials: true
		});
	} catch (error) {
		if (axios.isAxiosError(error)) {
			if (error.response) {
				const getResponse = error.response.data;
				openToast({
					message: handleErrorMessage(getResponse.status),
					status: "error"
				});
			} else {
				openToast({ message: " .", status: "error" });
			}
		}
		throw error;
	}
};

const SignUp: React.FC = () => {
	const navigate = useNavigate();
	const userContext = useContext(UserContext)!;

	const [id, setID] = useState<string>("");
	const [password, setPassword] = useState<string>("");
	const [checkPassword, setCheckPassword] = useState<string>("");
	const [name, setName] = useState<string>("");
	const [level, setLevel] = useState<number>(1);
	const [github, setGithub] = useState<string>("");

	const [duplication, setDuplication] = useState<boolean>(false);
	const [verifyPassword, setVerifyPassword] = useState<boolean>(true);
	const [syncPassword, setSyncPassword] = useState<boolean>(false);

	useEffect(() => {
		setSyncPassword(password !== checkPassword);
	}, [password, checkPassword]);

	const checkPasswordVerify = (e: ChangeEvent<HTMLInputElement>) => {
		const input = e.target.value;
		setPassword(input);
		if (passwordRegex.test(input)) {
			setVerifyPassword(false);
		} else {
			setVerifyPassword(true);
		}
	};

	const checkDuplicateID = async () => {
		try {
			const response = await DefaultAPI.post(
				"/auth/checkId",
				{ id: id },
				{ withCredentials: true }
			);
			if (response.status === 200) {
				setDuplication(false);
				openToast({ message: "   .", status: "success" });
			} else {
				setDuplication(true);
			}
		} catch (error) {
			if (axios.isAxiosError(error)) {
				if (error.response) {
					const getResponse = error.response.data;
					openToast({
						message: handleErrorMessage(getResponse.status),
						status: "error"
					});
				} else {
					openToast({ message: " .", status: "error" });
				}
			}
		}
	};

	const handleSignUp = async () => {
		if (!duplication && !syncPassword && !verifyPassword) {
			if (level && id) {
				const signUpInfo = {
					userId: id,
					password: password,
					passwordCheck: checkPassword,
					nickName: name,
					level: level
				};
				try {
					await apiSignUp(signUpInfo);
					userContext.setIsLoggedIn(true);
					navigate("/");
				} catch (error) {
					openToast({ message: " .", status: "error" });
				}
			}
		}
	};

	return (
		<Box w="55%" bg="white" p={10} borderColor="beige" borderRadius={15}>
			<Text fontSize="xl" textAlign="center" mb={10}>
				 ,   .
			</Text>
			<FormControl isRequired mb={5}>
				<FormLabel color="navy" fontWeight="Bold" fontSize="lg">
					ID
				</FormLabel>
				<Flex gap={3}>
					<Input
						type="text"
						bg="white"
						borderColor="beige"
						value={id}
						onChange={(e: ChangeEvent<HTMLInputElement>) =>
							setID(e.target.value)
						}
					/>
					<Button
						bg="deepBeige"
						variant="solid"
						color="white"
						onClick={checkDuplicateID}
					>
						 
					</Button>
				</Flex>
			</FormControl>
			<FormControl
				isRequired
				mb={5}
				isInvalid={verifyPassword && password.length > 0}
			>
				<FormLabel color="navy" fontWeight="Bold" fontSize="lg">
					Password
				</FormLabel>
				<Input
					type="password"
					bg="white"
					borderColor="beige"
					value={password}
					onChange={checkPasswordVerify}
				/>
				<FormErrorMessage>
					 , ,    8  
					.{" "}
				</FormErrorMessage>
			</FormControl>
			<FormControl
				isRequired
				mb={5}
				isInvalid={syncPassword && checkPassword.length > 0}
			>
				<FormLabel color="navy" fontWeight="Bold" fontSize="lg">
					Password Check
				</FormLabel>
				<Input
					type="password"
					bg="white"
					borderColor="beige"
					value={checkPassword}
					onChange={(e: ChangeEvent<HTMLInputElement>) =>
						setCheckPassword(e.target.value)
					}
				/>
				<FormErrorMessage>  .</FormErrorMessage>
			</FormControl>
			<FormControl isRequired mb={5}>
				<FormLabel color="navy" fontWeight="Bold" fontSize="lg">
					Name
				</FormLabel>
				<Input
					type="text"
					bg="white"
					borderColor="beige"
					value={name}
					onChange={(e: ChangeEvent<HTMLInputElement>) =>
						setName(e.target.value)
					}
				/>
			</FormControl>
			<FormControl isRequired mb={5}>
				<FormLabel color="navy" fontWeight="Bold" fontSize="lg">
					Level
				</FormLabel>
				<RadioGroup
					defaultValue={String(level)}
					onChange={value => setLevel(Number(value))}
					marginLeft={10}
					marginRight={10}
				>
					<Flex direction="row" justifyContent="space-between">
						<Radio colorScheme="orange" value="1">
							 1
						</Radio>
						<Radio colorScheme="orange" value="2">
							 2
						</Radio>
						<Radio colorScheme="orange" value="3">
							 3
						</Radio>
					</Flex>
				</RadioGroup>
			</FormControl>
			<FormControl mb={5}>
				<FormLabel color="navy" fontWeight="Bold" fontSize="lg">
					Github - Repository
				</FormLabel>
				<Input
					type="text"
					bg="white"
					borderColor="beige"
					value={github}
					onChange={(e: ChangeEvent<HTMLInputElement>) =>
						setGithub(e.target.value)
					}
				/>
			</FormControl>
			<Flex marginTop={2} justifyContent="right">
				<Button bg="navy" variant="solid" color="white" onClick={handleSignUp}>
					Signup
				</Button>
			</Flex>
		</Box>
	);
};

export default SignUp;
